import addFileIcon from "assets/img/fileUploadIcons/Add.svg";
import cancelImageEditIcon from "assets/img/fileUploadIcons/Cancel_ImageEdit.svg";
import addFileDisabledIcon from "assets/img/fileUploadIcons/Disabled_Add.svg";
import loaderIcon from "assets/img/fileUploadIcons/Loading.png";
import saveImageIcon from "assets/img/fileUploadIcons/Save_Image.svg";
import uploadedDocIcon from "assets/img/fileUploadIcons/Uploaded_Doc.svg";
import CropImage, { ICropImageRef } from "components/CropImage";
import DialogBox from "components/Dialog";
import ErrorDialog from "components/ErrorDialog";
import FileUploadMenuBox from "components/FileUploadMenuBox";
import { ViewMode } from "components/KYCV3";
import MediaGallery, {
  IMediaInfo,
  initialMediaGallerySectionInfo,
} from "components/MediaGallery/MediaGallery";
import PdfCustom from "components/PdfCustom";
import SimpleDropDown from "components/SimpleDropDown";
import Text from "components/Text";
import ToastCustom from "components/Toast";
import { DropdownChangeParams } from "primereact/dropdown";
import { ToastPositionType } from "primereact/toast";
import React, {
  Fragment,
  ReactNode,
  forwardRef,
  useCallback,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";
import FileUploadService from "services/FileUploadService";
import {
  parseStandardAPIErrorMessage,
  validateGetAPIResponse,
  validatePostAPIResponse,
} from "utils/CommonUtil";
import { DOCUMENT_CATEGORY, TOAST_STYLE_TYPE } from "utils/Constant";

interface IFileUploadCustomProps {
  className?: string;
  accept?: string[];
  docInfoForMediaGallery?: IMediaInfo; // to keep info for single upload
  docInfoListForMediaGallery?: IMediaInfo[]; // to keep info in a list for multiple uploads
  getUrl?: (uploadedFileDetails: IUploadedFile | any) => void;
  getSavedFileSignedUrl?: (signedUrl: string) => void; // used to get the signed url for a file in saved document section coming from API response
  footerTitle?: string;
  footerSubtitle?: string;
  disabled?: boolean; // to disable uploader
  // below props are used when this uploader component is used under document components
  mode?: ViewMode;
  isDummy?: boolean; //to act as a dummy uploader when multiple uploaders are required when it is true don't set the state of this component just return upload info via getUrl
  savedFileInfoFromApi?: { [key: string]: any }; //to show preview of uploaded file in view mode
  uploaderIndex?: number; //uploader index if rendered in a list
  clearCardUploadSectionData?: (section: string) => void; //delete the data stored in pageObj of component that renders fileUpload
  metaCategoryDropdownOptions?: IDropDownOptions[];
  metaTypeDropdownOptions?: IDropDownOptions[];
  metaSectionDropdownOptions?: IDropDownOptions[];
  getSelectedMetaOptions?: (data: any, idx: number) => any;
  disableMetaType?: boolean;
  disableMetaSection?: boolean;
  disableMetaCategory?: boolean;
  disableKebab?: boolean; // to disable the kebab menu based on card mode that is passed to the menu box
  mediaValidationRequired?: boolean;
  isExistDocUploader?: boolean;
  onClickUploader?: () => any;
}

export interface IUploadedFile {
  fileName: string;
  url: string;
  thumbUrl: string;
  signedUrl?: string;
  contentType: string;
  meta?: string;
}

interface IDropDownOptions {
  uid?: string;
  label: string;
  value: string;
}

interface IUploadStates {
  fileName: string;
  previewFileSignedUrl: string;
  thumbUrl: string;
  uploaded: boolean;
  uploadInProgress: boolean;
  uploadStarted: boolean;
  uploadType: string;
  url: string;
}

export interface IFileUploadCustomRef {
  isUrlAvailable: () => boolean;
  isSectionNameAvailable: () => boolean;
  resetErrorStates: () => void;
}

const FILE_TYPES = {
  IMAGE: "image",
  VIDEO: "video",
  PDF: "pdf",
  AUDIO: "audio",
  WORD_EXCEL: "application/vnd.openxmlformats-officedocument",
};

//helper variables
const initialUploadStates: IUploadStates = {
  uploadStarted: false,
  uploadInProgress: false,
  uploaded: false,
  previewFileSignedUrl: "",
  url: "",
  thumbUrl: "",
  fileName: "",
  uploadType: "",
};

const uploadedItemTitlePlaceHolder = "Select Meta";

let validationMetaInfo = {};

/*---------------------------------- Component ----------------------------------- */

const FileUploadCustom = forwardRef<IFileUploadCustomRef, IFileUploadCustomProps>((props, ref) => {
  const {
    getUrl,
    getSavedFileSignedUrl,
    disabled,
    footerTitle,
    mode,
    uploaderIndex,
    savedFileInfoFromApi,
    clearCardUploadSectionData,
    docInfoForMediaGallery,
    docInfoListForMediaGallery,
    metaCategoryDropdownOptions,
    metaTypeDropdownOptions,
    metaSectionDropdownOptions,
    disableMetaCategory,
    disableMetaSection,
    disableMetaType,
    disableKebab,
    getSelectedMetaOptions,
    mediaValidationRequired,
    isExistDocUploader,
    onClickUploader,
    isDummy,
  } = props;
  /* ---------------------------- Hooks ----------------------------------- */

  const inputRef = useRef<HTMLInputElement>(null);
  const imageCropperRef = useRef<ICropImageRef>(null);

  const [uploadInitiated, setUploadInitiated] = useState<boolean>(false); // to know file upload initiated or not
  const [fileUploaded, setFileUploaded] = useState<boolean>(false); // to know file has successfully uploaded or not
  const [uploadedItemTemplate, setUploadedItemTemplate] = useState<ReactNode>(<></>); // card view after file upload
  const [showUploadingText, setShowUploadingText] = useState<boolean>(false);
  const [cropImageSource, setCropImageSource] = useState<string | ArrayBuffer | null>("");
  const [croppedImageFile, setCroppedImageFile] = useState<File | null>(null);
  const [imageUploadStates, setImageUploadStates] = useState<IUploadStates>(initialUploadStates);
  const [pdfUploadStates, setPdfUploadStates] = useState<IUploadStates>(initialUploadStates);
  const [wordExcelDocStates, setWordExcelDocStates] = useState<IUploadStates>(initialUploadStates);
  const [audioUploadStates, setAudioUploadStates] = useState<IUploadStates>(initialUploadStates);
  const [videoUploadStates, setVideoUploadStates] = useState<IUploadStates>(initialUploadStates);
  const [showMediaGallery, setShowMediaGallery] = useState<boolean>(false);
  const [showEditMeta, setShowEditMeta] = useState<boolean>(false);
  const [selectedSectionValue, setSelectedSectionValue] = useState<string>("");
  const [selectedTypeValue, setSelectedTypeValue] = useState<string>("");
  const [selectedCategoryValue, setSelectedCategoryValue] = useState<string>("");
  const [uploadedItemTitle, setUploadedItemTitle] = useState<string>(uploadedItemTitlePlaceHolder);
  const [showEmptyStateError, setShowEmptyStateError] = useState<boolean>(false);
  const [showSelectMetaError, setShowSelectMetaError] = useState<boolean>(false);

  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right"); //NOSONAR
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.QUATERNARY);
  const [triggerToast, setTriggerToast] = useState(false);

  /*--------------------------API  Error States--------------------------- */
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  /*-----------------ref---------------- */
  const toastMessageRef = useRef(null);

  const menuOptions = [
    {
      label: "Edit Meta",
      command: () => {
        setShowEditMeta(true);
      },
    },
    {
      label: "Delete",
      command: () => {
        onDelete();
      },
    },
  ];

  // common upload template for all file uploads
  const uploadingFileTemplate = useCallback(() => {
    setUploadedItemTemplate(
      <div className="w-full h-full flex items-center justify-center bg-primary-ptext-200 border border-primary-ptext-600 rounded-xl">
        <div className="w-6 h-6 pointer-events-none cursor-pointer">
          <img className="animate-spin" src={loaderIcon} alt="loader" />
        </div>
      </div>
    );
    !isDummy ? setShowUploadingText(true) : null;

    setUploadInitiated(true);
  }, []);

  // common failed upload template for all file uploads
  const failedUploadFileTemplate = useCallback((failedMessage: string) => {
    setUploadedItemTemplate(
      <div className="w-full h-full bg-primary-ptext-200 flex border border-primary-ptext-600 rounded-xl items-center justify-center text-xxs">
        <div className="text-center text-semantic-crimson-600">
          <Text label={failedMessage} />
        </div>
      </div>
    );
    setShowUploadingText(false);
    // auto reset
    setTimeout(resetAllStates, 3000);
  }, []);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }),
    useEffect(() => {
      if (errorMessage) {
        setShowDialogAPIError(true);
      }
    }, [errorMessage]);

  useEffect(() => {
    if (isDummy) {
      resetAllStates();
    }
  });

  useEffect(() => {
    setFilePreview();
  }, [mode, savedFileInfoFromApi?.url]);

  useEffect(() => {
    if (disableMetaCategory || disableMetaType || disableMetaSection) {
      if (metaCategoryDropdownOptions?.length)
        setSelectedCategoryValue(metaCategoryDropdownOptions?.[0].value);
      if (metaTypeDropdownOptions?.length)
        setSelectedTypeValue(metaTypeDropdownOptions?.[0]?.value);
      if (
        metaSectionDropdownOptions &&
        uploaderIndex !== undefined &&
        metaSectionDropdownOptions.length > 1
      ) {
        setSelectedSectionValue(metaSectionDropdownOptions?.[uploaderIndex]?.value);
      } else {
        setSelectedSectionValue(metaSectionDropdownOptions?.[0]?.value ?? "NA");
      }
    }
  }, [metaCategoryDropdownOptions, metaTypeDropdownOptions, metaSectionDropdownOptions]);

  useEffect(() => {
    if (
      selectedSectionValue &&
      metaSectionDropdownOptions &&
      metaSectionDropdownOptions?.length === 1
    ) {
      // calling the below function to select the section instead of choosing it from meta dialog only if one section is present for a document type by default
      onSaveMetaDialog();
    }
  }, [selectedSectionValue]);

  useEffect(() => {
    if (croppedImageFile) {
      uploadImage(croppedImageFile);
    }
  }, [croppedImageFile]);

  useEffect(() => {
    if (imageCropperRef.current && !!cropImageSource) {
      imageCropperRef.current.toggler();
    }
  }, [cropImageSource]);

  //for managing image upload states
  useEffect(() => {
    const { uploaded, previewFileSignedUrl } = imageUploadStates;

    if (uploaded) {
      renderUploadedImage(previewFileSignedUrl);
      setShowUploadingText(false);
      setFileUploaded(true);
    }
  }, [imageUploadStates]);

  //for managing pdf upload states
  useEffect(() => {
    const { uploaded, previewFileSignedUrl } = pdfUploadStates;

    if (uploaded) {
      renderUploadedPdf(previewFileSignedUrl);
      setShowUploadingText(false);
      setFileUploaded(true);
    }
  }, [pdfUploadStates]);

  //for managing excel/word document upload states
  useEffect(() => {
    const { uploaded, previewFileSignedUrl } = wordExcelDocStates;

    if (uploaded) {
      renderUploadedWordExcel(previewFileSignedUrl);
      setShowUploadingText(false);
      setFileUploaded(true);
    }
  }, [wordExcelDocStates]);

  //for managing video upload states
  useEffect(() => {
    const { uploaded, previewFileSignedUrl } = videoUploadStates;

    if (uploaded) {
      renderUploadedVideo(previewFileSignedUrl);
      setShowUploadingText(false);
      setFileUploaded(true);
    }
  }, [videoUploadStates]);

  //for managing audio upload states
  useEffect(() => {
    const { uploaded, previewFileSignedUrl } = audioUploadStates;

    if (uploaded) {
      renderUploadedAudio(previewFileSignedUrl);
      setShowUploadingText(false);
      setFileUploaded(true);
    }
  }, [audioUploadStates]);

  useImperativeHandle(ref, () => ({
    isUrlAvailable: isFileUploaded, // to check if url for preview is available in the uploader or not
    isSectionNameAvailable: isSectionNameSelectedThroughMeta, // to check if the uploaded item has its meta options selected if yes then Select Meta label will disappear else it will be displayed
    resetErrorStates: resetErrorStates, // to hide the empty uploader error and meta selection error
  }));

  /* --------------------- Service Calls -------------------*/

  const setFilePreview = async () => {
    if (mode && savedFileInfoFromApi !== undefined) {
      const { fileType, contentType, url } = savedFileInfoFromApi;
      if (url) {
        //preview each saved file using signed url
        const signedURL = await getSignedUrlForPreview(url);
        const type = fileType ? fileType : contentType;
        setUploadInitiated(true);
        setFileUploaded(true);
        if (type.includes(FILE_TYPES.IMAGE)) {
          renderUploadedImage(signedURL);
          setImageUploadStates((prev) => ({
            ...prev,
            previewFileSignedUrl: signedURL,
            uploaded: true,
          }));
        } else if (type.includes(FILE_TYPES.PDF)) {
          renderUploadedPdf(signedURL);
          setPdfUploadStates((prev) => ({
            ...prev,
            previewFileSignedUrl: signedURL,
            uploaded: true,
          }));
        } else if (type.includes(FILE_TYPES.WORD_EXCEL)) {
          renderUploadedWordExcel(signedURL);
          setWordExcelDocStates((prev) => ({
            ...prev,
            previewFileSignedUrl: signedURL,
            uploaded: true,
          }));
        } else if (type.includes(FILE_TYPES.VIDEO)) {
          renderUploadedVideo(signedURL);
          setVideoUploadStates((prev) => ({
            ...prev,
            previewFileSignedUrl: signedURL,
            uploaded: true,
          }));
        } else if (type.includes(FILE_TYPES.AUDIO)) {
          renderUploadedVideo(signedURL);
          setAudioUploadStates((prev) => ({
            ...prev,
            previewFileSignedUrl: signedURL,
            uploaded: true,
          }));
        }

        //take the signed url for each file
        getSavedFileSignedUrl ? getSavedFileSignedUrl(signedURL) : null;
      }
    }
  };

  const getSignedUrlForPreview = async (url: string) => {
    const res = await FileUploadService.getSignedURL(url);
    try {
      if (validateGetAPIResponse(res)) {
        return res.data?.SignedUrl;
      } else {
        return "";
      }
    } catch (error) {
      console.log(error);
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
    return "";
  };

  const getFileSignedUrl = (
    objectUrl: string,
    fileInfo: IUploadedFile,
    uploadedFiletype: string,
    message: string
  ) => {
    FileUploadService.getSignedURL(objectUrl)
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          //invoke the callback received via prop to get the file url
          const prepareFileInfo = (fileInfo: {
            url: string;
            thumbUrl: string;
            fileName: string;
            contentType: string;
            signedUrl: string;
          }) => {
            const { url, thumbUrl, fileName, contentType, signedUrl } = fileInfo;
            getUrl
              ? getUrl({
                  url,
                  thumbUrl,
                  fileName,
                  contentType,
                  signedUrl,
                  meta: validationMetaInfo,
                })
              : null;
          };

          if (uploadedFiletype === "image") {
            !isDummy
              ? setImageUploadStates((prevState) => {
                  return {
                    ...prevState,
                    ...fileInfo,
                    previewFileSignedUrl: res.data?.SignedUrl,
                    uploaded: true,
                  };
                })
              : null;

            prepareFileInfo({
              url: fileInfo.url,
              thumbUrl: fileInfo.thumbUrl,
              fileName: fileInfo.fileName,
              contentType: fileInfo.contentType,
              signedUrl: res.data?.SignedUrl,
            });
          } else if (uploadedFiletype === "pdf") {
            !isDummy
              ? setPdfUploadStates((prevState) => {
                  return {
                    ...prevState,
                    previewFileSignedUrl: res.data?.SignedUrl,
                    uploaded: true,
                  };
                })
              : null;

            prepareFileInfo({
              url: fileInfo.url,
              thumbUrl: fileInfo.thumbUrl,
              fileName: fileInfo.fileName,
              contentType: fileInfo.contentType,
              signedUrl: res.data?.SignedUrl,
            });
          } else if (uploadedFiletype === "document") {
            !isDummy
              ? setWordExcelDocStates((prevState) => {
                  return {
                    ...prevState,
                    previewFileSignedUrl: res.data?.SignedUrl,
                    uploaded: true,
                  };
                })
              : null;

            prepareFileInfo({
              url: fileInfo.url,
              thumbUrl: fileInfo.thumbUrl,
              fileName: fileInfo.fileName,
              contentType: fileInfo.contentType,
              signedUrl: res.data?.SignedUrl,
            });
          } else if (uploadedFiletype === "video") {
            !isDummy
              ? setVideoUploadStates((prevState) => {
                  return {
                    ...prevState,
                    previewFileSignedUrl: res.data?.SignedUrl,
                    uploaded: true,
                  };
                })
              : null;

            prepareFileInfo({
              url: fileInfo.url,
              thumbUrl: fileInfo.thumbUrl,
              fileName: fileInfo.fileName,
              contentType: fileInfo.contentType,
              signedUrl: res.data?.SignedUrl,
            });
          } else if (uploadedFiletype === "audio") {
            !isDummy
              ? setAudioUploadStates((prevState) => {
                  return {
                    ...prevState,
                    previewFileSignedUrl: res.data?.SignedUrl,
                    uploaded: true,
                  };
                })
              : null;

            prepareFileInfo({
              url: fileInfo.url,
              thumbUrl: fileInfo.thumbUrl,
              fileName: fileInfo.fileName,
              contentType: fileInfo.contentType,
              signedUrl: res.data?.SignedUrl,
            });
          }
        } else {
          failedUploadFileTemplate(message);
        }
      })
      .catch((error) => {
        console.log(error);
        failedUploadFileTemplate(message);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      });
  };

  /*--------------------- Event handlers ------------------ */

  const onUpload = () => {
    if (isExistDocUploader) {
      return onClickUploader?.();
    }
    if (inputRef.current) {
      inputRef.current.click();
    }
  };

  const showImageCropper = (uploadedFile: File) => {
    const reader = new FileReader();

    reader.onload = () => {
      setCropImageSource(reader.result?.toString() || "");
    };

    reader.readAsDataURL(uploadedFile);
  };

  const getCroppedDataUrl = async (dataUrl: string, fileName = "sample.jpeg") => {
    const blob = await (await fetch(dataUrl)).blob();
    setCroppedImageFile(new File([blob], fileName, { type: blob.type }));
    setCropImageSource(null);
  };

  const onClickUploadedDoc = () => {
    setShowMediaGallery(true);
  };

  //image upload handler
  const uploadImage = (image: File) => {
    // assign null to the cropped file and prevent useEffect call for uploadImage() as it has dependency on croppedImageFile state
    setCroppedImageFile(null);

    // show progress(uploading) template
    uploadingFileTemplate();

    //generate an unique image name
    const timestamp = new Date().getTime();
    const randomString = Math.random().toString(36).substring(2, 8);
    const objectName = `${timestamp}_${randomString}`;

    FileUploadService.saveFile(image, objectName)
      .then((res) => {
        if (validatePostAPIResponse(res)) {
          const fileImageInfo = {
            url: res.data?.url ?? "",
            thumbUrl: res.data?.thumbUrl ?? "",
            fileName: res.data?.fileName ?? "",
            contentType: res.data?.contentType ?? "",
          };

          getFileSignedUrl(res.data?.url, fileImageInfo, "image", `Image upload failed`);
          setInterval(() => {
            getFileSignedUrl(res.data?.url, fileImageInfo, "image", `Image upload failed`);
          }, 1800 * 1000);
        } else {
          failedUploadFileTemplate(`Image upload failed`);
        }
      })
      .catch((error) => {
        console.log(error);
        failedUploadFileTemplate(`Image upload failed`);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      });
  };

  // pdf file upload handler
  const uploadPDF = (pdf: File) => {
    uploadingFileTemplate();

    //generate an unique pdf name
    const timestamp = new Date().getTime();
    const randomString = Math.random().toString(36).substring(2, 8);
    const objectName = `${timestamp}_${randomString}`;

    FileUploadService.saveFile(pdf, objectName)
      .then((res) => {
        if (validatePostAPIResponse(res)) {
          const filePdfInfo = {
            url: res.data?.url ?? "",
            fileName: res.data?.fileName ?? "",
            thumbUrl: "",
            contentType: res.data?.contentType ?? "",
          };

          getFileSignedUrl(res.data?.url, filePdfInfo, "pdf", `Pdf upload failed`);
          setInterval(() => {
            getFileSignedUrl(res.data?.url, filePdfInfo, "pdf", `Pdf upload failed`);
          }, 1800 * 1000);
        } else {
          failedUploadFileTemplate(`Pdf upload failed`);
        }
      })
      .catch((error) => {
        console.log(error);
        failedUploadFileTemplate(`Pdf upload failed`);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      });
  };

  //word/excel upload handler
  const uploadDocument = (document: File) => {
    uploadingFileTemplate();
    //generate an unique doc name
    const timestamp = new Date().getTime();
    const randomString = Math.random().toString(36).substring(2, 8);
    const objectName = `${timestamp}_${randomString}`;

    FileUploadService.saveFile(document, objectName)
      .then((res) => {
        if (validatePostAPIResponse(res)) {
          const fileWordExcelInfo = {
            url: res.data?.url ?? "",
            fileName: res.data?.fileName ?? "",
            thumbUrl: "",
            contentType: res.data?.contentType ?? "",
          };

          getFileSignedUrl(res.data?.url, fileWordExcelInfo, "document", `Document upload failed`);
          setInterval(() => {
            getFileSignedUrl(
              res.data?.url,
              fileWordExcelInfo,
              "document",
              `Document upload failed`
            );
          }, 1800 * 1000);
        } else {
          failedUploadFileTemplate(`Document upload failed`);
        }
      })
      .catch((error) => {
        console.log(error);
        failedUploadFileTemplate(`Document upload failed`);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      });
  };

  //audio upload handler
  const uploadAudio = (audio: File) => {
    uploadingFileTemplate();
    //generate an unique doc name
    const timestamp = new Date().getTime();
    const randomString = Math.random().toString(36).substring(2, 8);
    const objectName = `${timestamp}_${randomString}`;

    FileUploadService.saveFile(audio, objectName)
      .then((res) => {
        if (validatePostAPIResponse(res)) {
          const audioFileInfo = {
            url: res.data?.url ?? "",
            fileName: res.data?.fileName ?? "",
            thumbUrl: "",
            contentType: res.data?.contentType ?? "",
          };

          getFileSignedUrl(res.data?.url, audioFileInfo, "audio", `Audio upload failed`);
          setInterval(() => {
            getFileSignedUrl(res.data?.url, audioFileInfo, "audio", `Audio upload failed`);
          }, 1800 * 1000);
        } else {
          failedUploadFileTemplate(`Audio upload failed`);
        }
      })
      .catch((error) => {
        console.log(error);
        failedUploadFileTemplate(`Audio upload failed`);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      });
  };

  //video upload handler
  const uploadVideo = (vidFile: File) => {
    uploadingFileTemplate();

    FileUploadService.saveVideo(vidFile)
      .then((res) => {
        if (validatePostAPIResponse(res)) {
          let fileName = res.data?.videoUrl?.objectUrl ? res.data?.videoUrl?.objectUrl : "";
          fileName = fileName.split("video/");
          const ext = fileName?.[1]?.split(".");
          const videoFileInfo = {
            url: res.data?.videoUrl?.objectUrl ?? "",
            fileName: fileName?.[1] ?? "",
            thumbUrl: res.data?.videoUrl?.objectUrl ?? "",
            contentType: `video/${ext?.[1] ?? ""}`,
          };

          getFileSignedUrl(
            res.data?.videoUrl?.objectUrl,
            videoFileInfo,
            "video",
            `Video upload failed`
          );
          setInterval(() => {
            getFileSignedUrl(
              res.data?.videoUrl?.objectUrl,
              videoFileInfo,
              "video",
              `Video upload failed`
            );
          }, 1800 * 1000);
        } else {
          failedUploadFileTemplate(`Video upload failed`);
        }
      })
      .catch((error) => {
        console.log(error);
        failedUploadFileTemplate(`Video upload failed`);
        setErrorMessage(parseStandardAPIErrorMessage(error));
      });
  };

  //input file change handler
  const handleChange = (e: React.FormEvent<HTMLInputElement>) => {
    const target = e.target as HTMLInputElement & {
      files: FileList;
    };

    const uploadedFile = target.files[0];

    switch (uploadedFile.type) {
      case "application/pdf":
        uploadPDF(uploadedFile);
        break;

      case "image/jpeg":
      case "image/png":
        showImageCropper(uploadedFile);
        break;

      case "application/msword":
      case "application/vnd.openxmlformats-officedocument.wordprocessingml.document":
      case "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet":
        uploadDocument(uploadedFile);
        break;
    }

    if (uploadedFile.type.includes(FILE_TYPES.VIDEO)) {
      if (mediaValidationRequired) {
        const objectURL = URL.createObjectURL(uploadedFile);
        const video = document.createElement("video");
        video.src = objectURL;
        video.addEventListener("loadedmetadata", (e: any) => {
          const videoDuration = !isNaN(e?.target?.duration) ? Number(e?.target?.duration) : 0;
          validationMetaInfo = { durationInSec: videoDuration };
        });
      }

      uploadVideo(uploadedFile);
    } else if (uploadedFile.type.includes(FILE_TYPES.AUDIO)) {
      if (mediaValidationRequired) {
        const objectURL = URL.createObjectURL(uploadedFile);
        const audio = document.createElement("audio");
        audio.src = objectURL;
        audio.addEventListener("loadedmetadata", () => {
          const audioDuration = !isNaN(audio?.duration) ? Number(audio?.duration) : 0;
          validationMetaInfo = { durationInSec: audioDuration };
        });
      }

      uploadAudio(uploadedFile);
    } else if (!uploadedFile.type) {
      uploadDocument(uploadedFile);
    }
  };

  /*--------------------- Helper Functions -----------------*/

  const selectedMetaOptions = () => {
    const section: any = metaSectionDropdownOptions?.find(
      (section) => section.value === selectedSectionValue
    );
    const type: any = metaTypeDropdownOptions?.find(
      (section) => section.value === selectedTypeValue
    );
    const category: any = metaCategoryDropdownOptions?.[0];
    //call back to invoke after save

    if (uploaderIndex !== undefined) {
      getSelectedMetaOptions?.(
        {
          section: section ?? { label: "", uid: "", value: "" },
          type: type ?? { label: "", uid: "", value: "" },
          category: category ?? { label: "", uid: "", value: "" },
        },
        uploaderIndex
      );
    }
  };

  const resetAllStates = () => {
    setCroppedImageFile(null);
    setImageUploadStates(initialUploadStates);
    setPdfUploadStates(initialUploadStates);
    setWordExcelDocStates(initialUploadStates);
    setVideoUploadStates(initialUploadStates);
    setUploadInitiated(false);
    setFileUploaded(false);
    setShowEmptyStateError(false);
    setShowSelectMetaError(false);
  };

  const onDownload = (fileUrl: string) => {
    const randomString = Math.random().toString(36).substring(2);
    const downloadFileName = `file_${randomString}`;
    const link = document.createElement("a");
    link.href = fileUrl;
    link.download = `file-${downloadFileName}`;
    link.target = "_blank";
    link.rel = "noopener noreferrer";
    link.click();
  };

  const onDelete = () => {
    resetAllStates();
    setUploadedItemTemplate(emptyTemplate);
    if (clearCardUploadSectionData && footerTitle !== undefined) {
      clearCardUploadSectionData(footerTitle);
    }
  };

  const onCancelImageCroppingDialog = () => {
    setCropImageSource(null);
    setFileUploaded(false);
    setUploadInitiated(false);
    setUploadedItemTemplate(emptyTemplate);
  };

  const handleMetaDropDownChange = (e: DropdownChangeParams) => {
    if (e.target.name === "category") setSelectedCategoryValue(e.value);
    else if (e.target.name === "type") {
      setSelectedTypeValue(e.value);
    } else if (e.target.name === "section") {
      setSelectedSectionValue(e.value);
    }
  };

  const onSaveMetaDialog = () => {
    if (selectedCategoryValue !== DOCUMENT_CATEGORY.DOCUMENT_CATEGORY_KYC) {
      selectedMetaOptions();
    }

    const uploadTitle = metaSectionDropdownOptions?.find(
      (list) => list.value === selectedSectionValue
    );

    if (uploadTitle) {
      setUploadedItemTitle(uploadTitle.label);
      setShowSelectMetaError(false);
    } else {
      setUploadedItemTitle("Invalid");
    }
    setShowEditMeta(false);
  };

  const isFileUploaded = (): boolean => {
    if (
      imageUploadStates.previewFileSignedUrl ||
      pdfUploadStates.previewFileSignedUrl ||
      wordExcelDocStates.previewFileSignedUrl ||
      videoUploadStates.previewFileSignedUrl ||
      audioUploadStates.previewFileSignedUrl
    ) {
      return true;
    }
    setShowEmptyStateError(true);
    return false;
  };

  const isSectionNameSelectedThroughMeta = (): boolean => {
    if (uploadedItemTitle === uploadedItemTitlePlaceHolder && !footerTitle) {
      setShowSelectMetaError(true);
      return false;
    }
    return true;
  };

  const resetErrorStates = () => {
    setShowSelectMetaError(false);
    setShowEmptyStateError(false);
  };

  const getCurrentSection = () => {
    if (docInfoForMediaGallery && !docInfoListForMediaGallery) {
      return {
        ...(docInfoForMediaGallery ? docInfoForMediaGallery : initialMediaGallerySectionInfo),
      };
    } else if (docInfoListForMediaGallery && docInfoListForMediaGallery.length) {
      return {
        ...docInfoListForMediaGallery[uploaderIndex ?? 0],
      };
    }
  };

  /* ---------------------------- Sectional Renders ----------------------------------*/

  const emptyTemplate = (): ReactNode => {
    return (
      <button
        className={`w-25 h-25 flex items-center justify-center bg-primary-ptext-200 border border-primary-ptext-600 rounded-xl cursor-pointer ${
          disabled ? "pointer-events-none" : ""
        }`}
        onClick={onUpload}
        // changing the key to remount this template only in case of image uploads when the cropper dialog is cancelled
        // key={Math.random().toString(20)}
      >
        <input
          title="upload"
          ref={inputRef}
          className="hidden"
          type="file"
          accept={`application/pdf, application/msword, image/png, image/jpeg, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, video/*, audio/*`}
          onChange={handleChange}
        />

        <span className="select-none">
          <img src={!disabled ? addFileIcon : addFileDisabledIcon} alt="add-file" />
        </span>
      </button>
    );
  };

  const itemTemplate = (): ReactNode => {
    return (
      <div className={`w-25 h-25 flex cursor-pointer select-none`}>{uploadedItemTemplate}</div>
    );
  };

  const renderUploadedImage = (url: string) => {
    const imageTemplate: JSX.Element = (
      <div className="w-full flex justify-center">
        {url ? (
          <button
            className="w-full border border-primary-ptext-600 rounded-xl"
            onClick={onClickUploadedDoc}
          >
            <img className="w-full h-full rounded-xl object-fill" src={url} alt="image-preview" />
          </button>
        ) : (
          <div className="w-full bg-primary-ptext-300 text-center py-2 text-xxs text-semantic-crimson-600">
            <Text label={"Can't load preview"} />
          </div>
        )}
      </div>
    );
    setUploadedItemTemplate(imageTemplate);
  };

  const renderImageCropper = (): ReactNode => {
    if (cropImageSource) {
      return (
        <CropImage
          ref={imageCropperRef}
          imageSource={cropImageSource.toString()}
          getDataUrl={getCroppedDataUrl}
          onCancelImageCroppingDialog={onCancelImageCroppingDialog}
        />
      );
    }
  };

  const renderUploadedPdf = (url: string) => {
    const pdfTemplate: JSX.Element = (
      <div className="w-full flex justify-center">
        {url ? (
          <button title="upload" type="button" className="w-full" onClick={onClickUploadedDoc}>
            <PdfCustom source={url} />
          </button>
        ) : (
          <div className="w-full bg-primary-ptext-300 text-center py-2 text-xxs text-semantic-crimson-600">
            <Text label={"Can't load preview"} />
          </div>
        )}
      </div>
    );
    setUploadedItemTemplate(pdfTemplate);
  };

  const renderUploadedWordExcel = (url: string) => {
    const wordExcelTemplate: JSX.Element = (
      <div className="w-full border border-primary-ptext-600 rounded-xl">
        {url ? (
          <button onClick={onClickUploadedDoc}>
            <img src={uploadedDocIcon} alt="document" />
          </button>
        ) : (
          <div className="w-full bg-primary-ptext-300 text-center py-2 text-xxs text-semantic-crimson-600">
            <Text label={"Can't load preview"} />
          </div>
        )}
      </div>
    );
    setUploadedItemTemplate(wordExcelTemplate);
  };

  const renderUploadedVideo = (url: string) => {
    const videoUploadTemplate: JSX.Element = (
      <div className="w-full border border-primary-ptext-600 rounded-xl">
        {url ? (
          <button onClick={onClickUploadedDoc}>
            <img src={uploadedDocIcon} alt="video-thumbnail" />
          </button>
        ) : (
          <div className="w-full bg-primary-ptext-300 text-center py-2 text-xxs text-semantic-crimson-600">
            <Text label={"Can't load preview"} />
          </div>
        )}
      </div>
    );
    setUploadedItemTemplate(videoUploadTemplate);
  };

  const renderUploadedAudio = (url: string) => {
    const audioUploadTemplate: JSX.Element = (
      <div className="w-full border border-primary-ptext-600 rounded-xl">
        {url ? (
          <button onClick={onClickUploadedDoc}>
            <img src={uploadedDocIcon} alt="audio-thumbnail" />
          </button>
        ) : (
          <div className="w-full bg-primary-ptext-300 text-center py-2 text-xxs text-semantic-crimson-600">
            <Text label={"Can't load preview"} />
          </div>
        )}
      </div>
    );
    setUploadedItemTemplate(audioUploadTemplate);
  };

  const renderMetaDialog = (): ReactNode => {
    return (
      <DialogBox className="w-77 !px-0 !py-0" visible={showEditMeta} closable={false}>
        <div className="p-6 flex flex-col gap-4">
          <div className="mb-3 font-bold text-primary-primaryTextColor">
            <Text label={"Edit Meta"} />
          </div>
          <div className="w-64">
            <SimpleDropDown
              name="category"
              label={"Category"}
              labelClassNames="text-xs"
              optionLabel="label"
              optionValue="value"
              options={metaCategoryDropdownOptions}
              onChange={handleMetaDropDownChange}
              value={selectedCategoryValue}
              hideErrorRow
              disabled={disableMetaCategory}
            />
          </div>
          <div className="w-64">
            <SimpleDropDown
              name="type"
              label={"Type"}
              labelClassNames="text-xs"
              optionLabel="label"
              optionValue="value"
              options={metaTypeDropdownOptions}
              onChange={handleMetaDropDownChange}
              value={selectedTypeValue}
              hideErrorRow
              disabled={disableMetaType}
            />
          </div>
          <div className="w-64">
            <SimpleDropDown
              name="section"
              label={"Section"}
              labelClassNames="text-xs"
              optionLabel="label"
              optionValue="value"
              options={metaSectionDropdownOptions}
              onChange={handleMetaDropDownChange}
              value={selectedSectionValue}
              hideErrorRow
              disabled={disableMetaSection}
            />
          </div>

          <div className="mt-3 flex gap-6 items-center justify-center">
            <div className="flex flex-col text-xs items-center justify-center">
              <button className="w-12 h-12 cursor-pointer" onClick={() => setShowEditMeta(false)}>
                <img src={cancelImageEditIcon} alt="cancel" />
              </button>
              <Text label={"Cancel"} />
            </div>
            <div className="flex flex-col text-xs items-center justify-center">
              <button className="w-12 h-12 cursor-pointer" onClick={onSaveMetaDialog}>
                <img src={saveImageIcon} alt="save" />
              </button>
              <Text label={"Save"} />
            </div>
          </div>
        </div>
      </DialogBox>
    );
  };

  return (
    <React.Fragment>
      {/* gets rendered only when image is uploaded */}
      {cropImageSource && renderImageCropper()}
      <div className="flex flex-col relative max-w-fit">
        <div className="flex flex-col items-center gap-1 ">
          {uploadInitiated ? itemTemplate() : emptyTemplate()}

          {/* show loading text if the footer title is not provided via props */}
          {showUploadingText && !footerTitle && (
            <div className="w-full text-center text-xs text-primary-ptext-1000">
              <Text label={"Uploading"} />
            </div>
          )}
        </div>

        {footerTitle && (
          <div className="w-25 mt-3 text-center text-primary-ptext-1000 select-none">
            <p className="text-xs truncate" title={footerTitle}>
              {footerTitle}
            </p>
          </div>
        )}

        {fileUploaded && !showUploadingText && !footerTitle && (
          <div className="w-25 mt-3 flex flex-col items-center justify-center truncate select-none">
            <div
              className={`text-center text-xs ${
                uploadedItemTitle !== uploadedItemTitlePlaceHolder
                  ? uploadedItemTitle !== "Invalid"
                    ? "text-primary-ptext-1000"
                    : "text-semantic-crimson-600"
                  : "text-primary-ptext-700"
              } ${showSelectMetaError ? "text-semantic-crimson-600" : ""}`}
            >
              {uploadedItemTitle}
            </div>
          </div>
        )}

        {fileUploaded ? (
          <Fragment>
            <div className={`absolute top-2 right-2.5`}>
              <FileUploadMenuBox model={menuOptions} disabled={disableKebab} />
            </div>
            {renderMetaDialog()}
            <div className="h-4.75" />
          </Fragment>
        ) : (
          <Fragment>
            {showEmptyStateError && (
              <div className="text-xs text-semantic-crimson-600">
                <Text label={"Please upload doc"} />
              </div>
            )}
          </Fragment>
        )}

        {showMediaGallery ? (
          <MediaGallery
            visible={showMediaGallery}
            onToggleVisibility={() => setShowMediaGallery((prev) => !prev)}
            list={
              docInfoListForMediaGallery && docInfoListForMediaGallery.length
                ? docInfoListForMediaGallery
                : []
            }
            currentSection={getCurrentSection()}
          />
        ) : (
          <></>
        )}

        <ToastCustom
          position={position}
          ref={toastMessageRef}
          toastText={toastMessage}
          variant={toastMessageVariant}
        />

        <ErrorDialog
          onHide={() => {
            setShowDialogAPIError(false);
            setErrorMessage("");
          }}
          visible={showDialogAPIError}
          errorMessage={errorMessage}
        />
      </div>
    </React.Fragment>
  );
});

// Memoization
export default React.memo(FileUploadCustom);
