import CancelImageEditIcon from "assets/img/fileUploadIcons/Cancel_ImageEdit.svg";
import RotateIcon from "assets/img/fileUploadIcons/Rotate_Image.svg";
import SaveImageIcon from "assets/img/fileUploadIcons/Save_Image.svg";
import ZoomInIcon from "assets/img/fileUploadIcons/Zoom_In.svg";
import ZoomOutIcon from "assets/img/fileUploadIcons/Zoom_Out.svg";
import DialogBox from "components/Dialog";
import Text from "components/Text";
import React, { useEffect, useImperativeHandle, useRef, useState } from "react";
import ReactCrop, { Crop, PixelCrop, centerCrop, makeAspectCrop } from "react-image-crop";
import "react-image-crop/dist/ReactCrop.css";
import { canvasPreview } from "./canvasPreview";

interface ICropImage {
  imageSource: string;
  getDataUrl: (dataUrl: string, fileName?: string) => Promise<void>;
  onCancelImageCroppingDialog: () => void;
}

export interface ICropImageRef {
  toggler: () => void;
}

const CropImage: React.ForwardRefRenderFunction<ICropImageRef, ICropImage> = (
  props: ICropImage,
  ref
) => {
  const { imageSource, getDataUrl, onCancelImageCroppingDialog } = props;
  const [croppedImageSrc, setCroppedImageSrc] = useState("");
  const [visible, setVisible] = useState(false);
  const [crop, setCrop] = useState<Crop>({
    unit: "%",
    x: 0,
    y: 0,
    width: 100,
    height: 100,
  });
  const [completedCrop, setCompletedCrop] = useState<PixelCrop>();
  const imgRef = useRef<HTMLImageElement>(null);
  const [scale, setScale] = useState(1);
  const [rotate, setRotate] = useState(0);

  useImperativeHandle(ref, () => ({
    toggler: onToggleVisibility,
  }));

  useEffect(() => {
    const prepareCroppedImage = async () => {
      if (completedCrop?.width && completedCrop?.height && imgRef.current) {
        const croppedImage = await canvasPreview(imgRef.current, completedCrop, scale, rotate);
        if (croppedImage) {
          setCroppedImageSrc(croppedImage);
        }
      }
    };
    prepareCroppedImage();
  }, [completedCrop, scale, rotate]);

  // to center the cropper grid as per the required aspect ratio
  const centerAspectCrop = (mediaWidth: number, mediaHeight: number, aspect: number) => {
    return centerCrop(
      makeAspectCrop(
        {
          unit: "%",
          width: 90,
        },
        aspect,
        mediaWidth,
        mediaHeight
      ),
      mediaWidth,
      mediaHeight
    );
  };

  const onToggleVisibility = () => {
    setVisible((prev) => !prev);
  };

  const onSaveCroppedImage = async () => {
    if (croppedImageSrc) {
      getDataUrl(croppedImageSrc);
    } else {
      getDataUrl(imageSource);
    }

    onToggleVisibility();
  };

  const onCancel = () => {
    onToggleVisibility();
    onCancelImageCroppingDialog();
  };

  const onImageRotate = () => {
    setRotate((prev) => {
      if (prev <= -270) {
        return 0;
      }
      return prev - 90;
    });
  };

  return (
    <div className="container">
      <DialogBox
        className="image-cropper-dialog !w-auto !max-h-11/12 !px-0 !py-0 overflow-y-auto"
        visible={visible}
        closable={false}
      >
        <div className="flex flex-col ">
          <div className="pl-6 py-2 font-bold text-base text-primary-primaryTextColor">
            <Text label={"Edit Image"} />
          </div>

          <div className="bg-white px-6 pt-4 pb-2 flex justify-center border-2 border-l-0 border-r-0 border-b-0">
            {!!imageSource && (
              <ReactCrop
                className="w-full"
                crop={crop}
                onChange={(_, percentCrop) => {
                  setCrop(percentCrop);
                }}
                onComplete={(c) => {
                  setCompletedCrop(c);
                }}
                ruleOfThirds
                aspect={undefined}
              >
                <img
                  ref={imgRef}
                  alt="image"
                  src={imageSource}
                  style={{
                    transform: `scale(${scale}) rotate(${rotate}deg)`,
                    // objectFit: "contain",
                  }}
                />
              </ReactCrop>
            )}
          </div>

          <div className="flex justify-center border-2 border-l-0 border-r-0 border-t-0">
            {/* crop control box */}

            <div className="w-full px-6 pb-2 flex justify-between items-center">
              <button className="cursor-pointer" onClick={onImageRotate}>
                <img src={RotateIcon} alt="rotate" />
              </button>

              <div className="h-6 right-control rounded-lg flex gap-1 px-2 bg-secondary-steelBlue-1000">
                <button
                  className="w-3 self-center zoom-out-control cursor-pointer"
                  onClick={() => {
                    setScale((prev) => {
                      if (prev >= 1.1) {
                        return Math.round((prev - 0.1) * 10) / 10;
                      } else return 1;
                    });
                  }}
                >
                  <img src={ZoomOutIcon} alt="zoomOut" />
                </button>

                <input
                  className="cropper-zoom-range"
                  type="range"
                  value={scale}
                  min={1}
                  max={3}
                  step={0.1}
                  aria-labelledby="Zoom"
                  onChange={(e: React.BaseSyntheticEvent) => setScale(Number(e.target.value))}
                />

                <button
                  className="w-3 self-center zoom-in-control cursor-pointer"
                  onClick={() => {
                    setScale((prev) => {
                      if (prev <= 2.9) {
                        return Math.round((prev + 0.1) * 10) / 10;
                      } else return 3;
                    });
                  }}
                >
                  <img src={ZoomInIcon} alt="zoomIn" />
                </button>
              </div>
            </div>
          </div>

          <div className="self-center py-3 flex gap-5">
            <div className="flex flex-col text-xs items-center justify-center">
              <button className="w-12 h-12 cursor-pointer" onClick={onCancel}>
                <img src={CancelImageEditIcon} alt="cancel" />
              </button>
              <span className="-mt-1 pr-1">
                <Text label={"Cancel"} />
              </span>
            </div>
            <div className="flex flex-col text-xs items-center justify-center">
              <button className="w-12 h-12 cursor-pointer" onClick={onSaveCroppedImage}>
                <img src={SaveImageIcon} alt="save" />
              </button>
              <span className="-mt-1 pr-1">
                <Text label={"Save"} />
              </span>
            </div>
          </div>
        </div>
      </DialogBox>
    </div>
  );
};

export default React.forwardRef(CropImage);
